// import { CetusFarmsSDK } from '@cetusprotocol/farms-sdk'
// import { CetusDcaSDK } from '@cetusprotocol/dca-sdk'
import { useSdk, useSdkStore } from '@cetus/sdk-factory'
import { useEffect } from 'react'

function Test() {
  const { account, setAccount, currentRpc, setCurrentRpc, rpcs, setRpcs } = useSdkStore()
  const clmmSdk = useSdk('clmm')

  useEffect(() => {
    setCurrentRpc('https://fullnode.mainnet.sui.io')
  }, [])

  const handleGetAssignPools = () => {
    clmmSdk?.Pool.getAssignPools(['0xcf994611fd4c48e277ce3ffd4d4364c914af2c3cbb05f7bf6facd371de688630'])
      .then((res: any) => {
        console.log('handleGetAssignPools###res: ', res)
      })
      .catch((error: any) => {
        console.log('handleGetAssignPools###error: ', error)
      })
  }

  return (
    <VStack w="100vw" h="100vh" background="bg_primary" p="24px">
      <Switch />
      <Button variant="ghost">button</Button>

      <Button variant="outline">button</Button>

      <Button variant="solid" onClick={handleGetAssignPools}>
        get pools
      </Button>
      <Icon xlinkHref="#icon-icon_marketdetails" />
    </VStack>
  )
}

export default Test
